<template>
    <div id="header">
        <el-row type="flex" justify="space-between">
            <el-col :span="10" id="header-left">
                <img class="icon" src="../../assets/image/studio.jpg">
                <span class="title">新媒体工作室</span>
            </el-col>
            <el-col :span="4" id="header-center">
                <span class="title">信息收集表</span>
            </el-col>
            <el-col :span="10" id="header-right">
                <span class="title">欢迎各位新老工作室的成员！</span>
            </el-col>
        </el-row>
    </div>
</template>

<script>
    export default {
        name: 'Header'
    }
</script>

<style lang="stylus" scoped>
    #header
        height 1rem
        background-color #0064a9
        color #fff
        box-shadow 0px 0px 12px 0px rgba(0,0,0,0.2)
        #header-left
            height 1rem
            padding-left .8rem
            // background-color red
            display flex
            align-items center
            .icon
                height .7rem
                width .7rem
                border-radius 50%
                margin-right .4rem
        #header-center
            height 1rem
            // background-color silver
            display flex
            justify-content center
            align-items center
        #header-right
            height 1rem
            padding-right .8rem
            // background-color orange
            display flex
            justify-content flex-end
            align-items center
        .title
            font-size .36rem
</style>


