import Vue from 'vue'
import Router from 'vue-router'
import CollectInfo from '@/collectInfo/CollectInfo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'CollectInfo',
      component: CollectInfo
    }
  ]
})



